* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
}

body {
  background: #000;
  color: #fff;
}

.background-canvas {
  border: 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.intro-prompt-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100%;
}

h1 {
  font-weight: 400;
}

.response-text-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.answer-text-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.responses {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.question-wrapper-x {
  display: flex;
  flex-direction: row;
}

.question-wrapper-y {
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

.spacer-x {
  flex-grow: 0;
  flex-shrink: 1;
}

.spacer-y {
  flex-grow: 0;
  flex-shrink: 1;
}

.question {
  background: red;
}

.response-wrapper {
  display: flex;
  flex-direction: row;
}
